<!-- eslint-disable-->
<template>
<main class="content container">
  <div class="content__top">
    <ul class="breadcrumbs">
      <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> Корзина </router-link>
        </li>
      <li class="breadcrumbs__item">
        <a class="breadcrumbs__link">
          Оформление заказа
        </a>
      </li>
    </ul>

    <h1 class="content__title">
      Корзина
    </h1>
    <span class="content__info">
      {{ totalProducts }} товара
    </span>
  </div>

  <section class="cart">
    <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
      <div class="cart__field">
        <div class="cart__data">
          <BaseFormText v-model="formData.name"  title="ФИО" :error="formError.name" placeholder="Введите ваше имя"/>
          <BaseFormText v-model="formData.address"   title="Адрес доставки" :error="formError.address" placeholder="Введите ваше адрес"/>
          <BaseFormText v-model="formData.phone" type="tel" title="Телефон" :error="formError.phone" placeholder="Введите ваше телефон"/>
          <BaseFormText v-model="formData.email" type="email" title="Email" :error="formError.email" placeholder="Введите ваше Email"/>
          <BaseFormTextArea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment" placeholder="Ваши пожелания"/>
        </div>

        <div class="cart__options" v-if="isCartOptionsShown">
          <h3 class="cart__title">Доставка</h3>
          <ul class="cart__options options">
            <li class="options__item">
              <label class="options__label">
                <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                <span class="options__value">
                  Самовывоз <b>бесплатно</b>
                </span>
              </label>
            </li>
            <li class="options__item">
              <label class="options__label">
                <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                <span class="options__value">
                  Курьером <b>500 ₽</b>
                </span>
              </label>
            </li>
          </ul>

          <h3 class="cart__title">Оплата</h3>
          <ul class="cart__options options">
            <li class="options__item">
              <label class="options__label">
                <input class="options__radio sr-only" type="radio" name="pay" value="card">
                <span class="options__value">
                  Картой при получении
                </span>
              </label>
            </li>
            <li class="options__item">
              <label class="options__label">
                <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                <span class="options__value">
                  Наличными при получении
                </span>
              </label>
            </li>
          </ul>
        </div>
      </div>

      <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in products" :key="item.product.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого:
              <b>{{ totalProducts | numberFormat }}</b>
              товара на сумму
              <b>{{ totalPrice | numberFormat }} ₽</b>
            </p>
          </div>

        <button class="cart__button button button--primery" type="submit">
          Оформить заказ
        </button>
      </div>
      <div class="lds-spinner" v-if="orderLoading">
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
          <div></div>
        </div>
      <div class="cart__error form__error-block" v-if="formErrorMessage">
        <h4>Заявка не отправлена!</h4>
        <p>
          {{ formErrorMessage }}
        </p>
      </div>
    </form>
  </section>
</main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { BaseFormText, BaseFormTextArea },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      isCartOptionsShown: false,
      orderLoading: false,
    };
  },
  filters: {
    numberFormat,
  },
  methods: {
    gotoPage,
    order() {
      this.orderLoading = true;
      this.formError = {};
      this.formErrorMessage = '';

      axios.post(`${API_BASE_URL}/api/orders`, {
        ...this.formData,
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey,
        },
      })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(() => { this.orderLoading = false; });
    },
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalProducts: 'cartTotalProducts',
    }),
  },
};
</script>
